import React from 'react'
import ReactMarkdown from 'react-markdown'

import summary from '../content/output/summary.json'
// import {Router} from '../routes'

import Link from 'next/link'
import Footer from './footer'

class Project extends React.Component {

  static async getInitialProps(req) {
    const id = req.query.id
    return { id: id }
  }

  render() {
    const project = require(`../content/output/projects/${this.props.id}.json`)

    return (
      <div>
        <title>{ project.title }</title>
        <div className="ui stackable grid" style={{ marginTop: '20px' }}>
          <div className="one wide column"></div>

          <div id="project" className="ten wide column centered">

            <div id="breadcrumb" className="ui breadcrumb">
              <a className="section" href="/">Ryo Suzuki</a>
              <i className="right angle icon divider"></i>
              <a className="active section">{ project.name }</a>
            </div>

            <h1 className="title">{ project.title }</h1>
            <p>
            { project.authors
              .map((author) => (author === 'Ryo Suzuki') ? <strong>{author}</strong> : <span>{author}</span>)
              .reduce((prev, curr) => [prev, ', ', curr])}
            </p>
            <p>
              <a href={ project.conference.url }>
                <b>{ project.conference.fullname }</b>
              </a>
            </p>
            <p>
              Links: &nbsp;
                <a className="ui label" href={ '/publications/' + project.pdf } target="blank" style={{ marginRight: '5px', display: project.pdf ? 'inline' : 'none' }}>[PDF]</a>
                <a href={ project.video } target="blank" style={{ marginRight: '5px', display: project.video ? 'inline' : 'none' }}>[Video]</a>
                <a href={ project['short-video'] } target="blank" style={{ marginRight: '5px', display: project['short-video'] ? 'inline' : 'none' }}>[Short Video]</a>
                <a href={ '/publications/' + project.slide } target="blank" style={{ marginRight: '5px', display: project.slide ? 'inline' : 'none' }}>[Slide]</a>
                <a href={ project.github } target="blank" style={{ marginRight: '5px', display: project.github ? 'inline' : 'none' }}>[GitHub]</a>
                <a href={ project['acm-dl'] } target="blank" style={{ marginRight: '5px', display: project['acm-dl'] ? 'inline' : 'none' }}>[ACM DL]</a>
                <a href={ project['ieee'] } target="blank" style={{ marginRight: '5px', display: project['ieee'] ? 'inline' : 'none' }}>[IEEE]</a>
                <a href={ project['talk'] } target="blank" style={{ marginRight: '5px', display: project['talk'] ? 'inline' : 'none' }}>[Talk]</a>
              <a className="ui label" href={ project.pdf } target="_blank">PDF</a> &nbsp;
              <a className="ui label" href={ project.doi } target="_blank">DOI</a> &nbsp;
              <a className="ui label" href={ project.slide } target="_blank">Slide</a> &nbsp;
              <a className="ui label" href={ project.video } target="_blank">Video</a>
            </p>

            <ReactMarkdown
              source={project.bodyContent}
              escapeHtml={false}
              linkTarget="_blank"
            />

            <h1>Video Preview</h1>
            <div class="video-container">
              <iframe class="embed" width="100%" height="315" src={`${project.embed}?autoplay=1&mute=1&rel=0&loop=1`} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
            </div>

            <h1>Publication</h1>
            <div class="ui placeholder segment">
              <p>
                { project.authors.join(', ').replace(/, ([^,]*)$/, ', and $1') }. { project.year }. <b>{ project.title }</b>. <i>{ project.booktitle }</i>. { project.publisher }, { project.pages }.<br/>DOI: <a href={ project.doi } target="_blank">{ project.doi }</a>
              </p>
            </div>

            <div>
              <a class="paper-thumbnail" href={ `/publications/${project.pdf}`} target="_blank">
                <div class="figures ui six column grid">
                  { [...Array(12)].map((v, i) => {
                    return (
                      <img class="paper column" src={`/static/projects/${project.name}/paper-${i+1}.png`} />
                    )
                  })}
                </div>
              </a>
            </div>

            <h1>Slide</h1>
            <div class="figures ui six column grid">
              { [...Array(12)].map((v, i) => {
                const src = `/static/projects/${project.name}/slide-${i+1}.png`
                return (
                  <a class="paper column" href={src} data-lightbox="lightbox">
                    <img src={src} />
                  </a>
                )
              })}
            </div>

          </div>
          <div className="one wide column"></div>
        </div>
        <Footer />
      </div>
    )
  }
}

export default Project
